body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* Ecom */

/* Navbar */

.main-login{
  margin: 40px auto;
  width: 600px;
}
.align-left ul{
  margin: 0 !important;
  float: right !important;
}
.align-left ul li{
  float: right !important;
}

.main-signup{

  margin: 40px auto;
  width: 60%;
}
.products{
margin: 0 auto;
width: 90%;
}
.product-img{
  width: 90%;
  height: auto;
}
.pcard{
  margin: 10px 5px ;
}
.cbody{
  padding: 8px; 
}
.cbody p{
  margin: 0;
}
.slide{
  float: left;
  height: 400px;
  width: 100%;
  overflow: hidden;
}
/* .slide {
  float: left;
  height: 200px;
  width: 100%;
  overflow: hidden;
} */
.nav-ul li{
display: inline;

}



/* Media Queries */
/* @media (min-width: 576px) { ... } */
/* @media (min-width: 768px) { ... } */
@media (min-width: 992px) { 
  .nav-ul{
    margin: 0 0 0 80px;

  }
  /* sign up */
  .product-form
  .main-signup{

    margin: 40px auto;
    width: 45%;
  }
  

}
@media (min-width: 1200px) { 
  .nav-ul{
    margin: 0 0 0 170px;
  }
  .product-form{
    width: 40%;
    margin: 40px auto;
  }

  .main-signup {

    margin: 40px auto;
    width: 45%;
  }
}
